@import url(https://fonts.googleapis.com/css2?family=Yuji+Syuku&display=swap);
*{font-family:"Yuji Syuku",serif;text-decoration:none}.navbar{background-image:linear-gradient(-90deg, #030303, #015d53)}.nav-link{color:#fff !important;cursor:pointer}button{background-color:#0088a9;color:#fff !important;border:none;border-radius:50px;cursor:pointer;transition:all .3s ease 0s}button:hover{background-color:rgba(0,136,169,.8)}img{padding-right:5px}.modalBackground{width:100vw;height:100vh;background-color:#c8c8c8;position:fixed;display:flex;justify-content:center;align-items:center;z-index:1000}.modalContainer{width:500px;height:500px;border-radius:12px;background-color:#fff;box-shadow:rgba(0,0,0,.35) 0px 5px 15px;display:flex;flex-direction:column;padding:25px;z-index:1000}.hero-container .hero{width:100%;height:50%;opacity:.4}.hero-container .hero-text{z-index:100;position:absolute;color:#015d53;font-size:35px;font-weight:bold;left:100px;right:100px;top:350px}.reg-container{padding-top:20px}.login-container{padding-top:20px}
